import Vue from 'vue'
import VueRouter from 'vue-router'
import Todos from '@/views/Todos.vue'

Vue.use(VueRouter)

const routes = [{
    path: '/',
    name: 'Todos',
    component: Todos,
    meta: {
        title: 'Todos'
    },
}, ]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
